import React, { Component } from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import SignUp from "./Containers/SignUp/SignUp";
import Login from "./Containers/Login/Login";
import Toolbar from "./Components/Navigation/Toolbar/Toolbar";
import Home from "./Components/Home/Home";
import Mapi from "./Components/Map/map";
import Driver from './Components/Driver/Driver';
import Rider from './Components/Rider/Rider';
import RiderDetail from './Components/Rider/RiderDetail';
import DriverDetail from './Components/Driver/DriverDetail';



import News from './Components/News/News';
import "./App.css";

class App extends Component {

  state = {
    loggedIn : window.localStorage.getItem('taxi.auth') !== null,
  }

  logIn = () =>   {
    this.setState({loggedIn:true})
  }

  logOut = () => {
    window.localStorage.removeItem('taxi.auth');
    this.setState({loggedIn:false,value:'abc'})
  };

  render() {
    return (
      <div className="app">
        <Toolbar isLoggedIn={this.state.loggedIn} value={this.state.value} logout={this.logOut}/>
        <Switch>
          <Route path="/news" exact component={News} />
          <Route path="/map" exact component={Mapi} />
          <Route path="/driver" exact component={Driver} />
          <Route path="/rider" exact component={Rider} />
          <Route path='/rider/:id' component={RiderDetail} />
          <Route path='/driver/:id' component={DriverDetail} />

          <Route path="/signup" render={() => (
            !this.state.loggedIn  ? (
              <SignUp subitt/>
            ) : (
              <Redirect to='/' />
            )
          )}/>

          <Route path="/login" render={() => (
            !this.state.loggedIn ? (
              <Login login={this.logIn} />
            ) : (
              <Redirect to='/' />
            )
          )}/>
          <Route path="/" exact render={() =>  <Home isLoggedIn={this.state.loggedIn}/>} />
        </Switch>
      </div>
    );
  }
}

export default App;
