import React, { useEffect, useState } from 'react';
import {
  Card, Col, Row
} from 'react-bootstrap';

import TripMedia from '../Driver/TripMedia';
import { getTrip } from '../../Containers/Services/tripService';

function RiderDetail({ match }) {
  const [trip, setTrip] = useState(null);

  useEffect(() => {
    const loadTrip = async (id) => {
      const { response, isError } = await getTrip(id);
      if (isError) {
        setTrip(null);
      } else {
        setTrip(response.data);
      }
    }
    loadTrip(match.params.id);
  }, [match]);

  let tripMedia;

  if (trip === null) {
    tripMedia = <>Loading...</>;
  } else {
    tripMedia = (
      <TripMedia
        trip={trip}
        otherGroup='driver'
      />
    )
  }

  return (
    <div class="riderDetail" style={{ marginTop: "100px" }}>
      <Row>
        <Col lg={12}>
          <Card className='mb-3' data-cy='trip-card'>
            <Card.Header>Trip</Card.Header>
            <Card.Body>{tripMedia}</Card.Body>
          </Card>
        </Col>
      </Row>
    </div>
  );
}

export default RiderDetail;