import React from 'react';
import NavigationItem from '../NavigationItem';
import './NavigationItems.css';
import { isDriver } from '../../../Containers/Services/authService';
import { isRider } from '../../../Containers/Services/authService';

const navigationItems = (props) => {
    // var log= <div><p onClick={props.isLoggedOut}> Logout</p></div>
    var log= <div><div><NavigationItem click={props.isLoggedOut} link="/login"  className="hamburger">Logout</NavigationItem></div></div>
    var dashboard=null;
    if(props.isLoggedIn === false)
    {log=<div><NavigationItem  link="/login" className=" " exact> Login </NavigationItem></div>}
    if(props.isLoggedIn === true && isDriver())
    {dashboard=<NavigationItem  link="/driver" classname="hamburger" exact >Dashboard</NavigationItem>}
    if(props.isLoggedIn === true && isRider())
    {dashboard=<NavigationItem  link="/rider" classname="hamburger" exact >Dashboard</NavigationItem>}

    return (
        <div className="rightNav">
            {dashboard}
            {log}       
        </div>    
    );
};

export default navigationItems;

