import React, { useEffect, useState } from 'react';
import {
  Col, Row
} from 'react-bootstrap';

import TripCard from './TripCard';
import { getTrips } from '../../Containers/Services/tripService';
import { webSocket } from 'rxjs/webSocket';
import { getAccessToken } from '../../Containers/Services/authService';


function DriverDashboard (props) {
  const [trips, setTrips] = useState([]);

  useEffect(() => {
    const token = getAccessToken();
    const ws = webSocket(`ws://localhost:8000/taxi/?token=${token}`);
    const subscription = ws.subscribe((message) => {
      setTrips(prevTrips => [
        ...prevTrips.filter(trip => trip.id !== message.data.id),
        message.data
      ]);
    });
    return () => {
      subscription.unsubscribe();
    }
  }, []);

  useEffect(() => {
    const loadTrips = async () => {
      const { response, isError } = await getTrips();
      if (isError) {
        setTrips([]);
      } else {
        setTrips(response.data);
      }
    }
    loadTrips();
  }, []);


  const getCurrentTrips = () => {
    return trips.filter(trip => {
      return trip.driver !== null && trip.status !== 'COMPLETED';
    });
  }

  const getRequestedTrips = () => {
    return trips.filter(trip => {
      return trip.status === 'REQUESTED';
    });
  }

  const getCompletedTrips = () => {
    return trips.filter(trip => {
      return trip.status === 'COMPLETED';
    });
  }

  return (
    <div class="driver" style={{marginTop:"100px"}}>
      <Row>
        <Col lg={12}>
      
          <TripCard
            title='Current Trip'
            trips={getCurrentTrips()}
            group='driver'
            otherGroup='rider'
          />
          <TripCard
            title='Requested Trips'
            trips={getRequestedTrips()}
            group='driver'
            otherGroup='rider'
          />
          <TripCard
            title='Recent Trips'
            trips={getCompletedTrips()}
            group='driver'
            otherGroup='rider'
          />
        </Col>
      </Row>
    </div>
  );
}

export default DriverDashboard;